<template>
  <nav aria-label="Page navigation example">
    <ul class="pagination">
      <li class="page-item" :class="{ disabled: !page.has_pre }">
        <a
          @click.prevent="$emit('page-change', page.current_page - 1)"
          class="page-link"
          href="#"
          aria-label="Previous"
        >
          <span aria-hidden="true">&laquo;</span>
        </a>
      </li>
      <li
        :class="{ active: item === page.current_page }"
        class="page-item"
        v-for="item in page.total_pages"
        :key="item"
      >
        <a class="page-link" href="#" @click.prevent="$emit('page-change', item)">{{ item }}</a>
      </li>
      <li class="page-item" :class="{ disabled: !page.has_next }">
        <a
          @click.prevent="$emit('page-change', page.current_page + 1)"
          class="page-link"
          href="#"
          aria-label="Next"
        >
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  props: ['page'],
};
</script>
